import React from "react";

import Dic from "../../../lenguage.handler.component";

export default class MyForm extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: "",
    };
  }

  render() {
    const { status } = this.state;
    return (
      <form
        onSubmit={this.submitForm}
        action="https://formspree.io/f/mbjpallo"
        method="POST"
      >
        <label>{Dic[4].about.titleForm}</label>
        <input
          type="email"
          className="email"
          name="email"
          placeholder={Dic[4].about.mailForm}
        />
        <input
          type="text"
          className="subject"
          name="subject"
          placeholder={Dic[4].about.msgForm}
        />
        {status === "SUCCESS" ? (
          <p>{Dic[4].about.succForm}</p>
        ) : (
          <button>{Dic[4].about.buttonForm}</button>
        )}
        {status === "ERROR" && <p>{Dic[4].about.errForm}</p>}
      </form>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}
